.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
}

.logic {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
      flex-flow: row wrap;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-item-align: stretch;
      align-self: stretch;
  padding: 10px 0;
}

.logic button {
  margin: 10px;
  background: none;
  border: 1px solid orange;
  cursor: pointer;
}

.calculate-css {
  color: black;
  text-shadow: 8px 5px 2px silver;
  text-align: center;
}

.calculate span {
  margin: auto 20px;
}

input {
  margin: 8px;
  border: none;
  -webkit-box-shadow: 0 0 5px 1px orange;
          box-shadow: 0 0 5px 1px orange;
  padding: 6px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


